import React from 'react';
import s from './Post.module.css';
export type MessageProps = {
  message: string
  likesCount: number
}
export const Post = (props: MessageProps) => {
   return (
    <div className={s.item}>
      <img src='data:image/jpeg;base64,'/>
      { props.message }
      <div>
      <span>like</span> { props.likesCount }
    </div>
    </div>
)
}